import Sidebar from '@/components/admin/sidebar';
import Header from '@/components/admin/Header';
import { Toaster } from '@/components/ui/sonner'; // Jangan lupa Toaster notifikasi

export default function AdminLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    return (
        <div className="min-h-screen bg-gray-50 flex">
            
            {/* 1. Sidebar (Fixed di Kiri) */}
            <Sidebar />

            {/* 2. Area Konten Utama */}
            {/* md:ml-64 memberikan margin kiri selebar sidebar agar konten tidak tertutup */}
            <div className="flex-1 md:ml-64 flex flex-col min-h-screen transition-all duration-300">
                
                {/* 3. Header (Fixed di Atas) */}
                <Header />

                {/* 4. Isi Halaman (Children) */}
                {/* mt-16 memberikan margin atas setinggi header */}
                <main className="flex-1 p-6 md:p-8 mt-16 overflow-y-auto">
                    {children}
                </main>
                
            </div>

            {/* 5. Komponen Notifikasi Global */}
            <Toaster />
        </div>
    );
}